@import '~antd/dist/antd.css';

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.paint-content {
  height: initial;
  margin-top: 64px;
  background: #f0f2f5;
}

.paint-logo{
  background: rgba(255,255,255,.2);
  margin: 16px;
  cursor: pointer;
}

.taobaoimg{
  width: 100%;
}

.frame-canvas{
  padding-left: 12px;
  transform: scale(4);
}
.content-wrap{
  padding-top: 24px;
  padding-bottom: 24px;
  height: 100%;
}

.pixel-editor{
  cursor: crosshair;
}

.topic-title{
  height: 32px;
  line-height: 32px;
  text-align: center;
}

.paint-row{
  margin: 10px;
}

.color-display{
  width: 30px;
  height: 30px;
  border-radius: 15px;
  border: 1px #5B5B5B solid
}

.color-cover{
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}



@media screen and (max-width: 767px) {
  .paint-content {
    margin-top: 16px;
  }
}
